const ADD_MESEGE = 'ADD-MESEGE';

let initialState = {
  mesegeDate: [
    {id: 1, name: 'How are you?'}, 
    {id: 2, name: 'Im fine'}, 
    {id: 3, name: 'No bed'}
  ],
  diaDate: [
    {id: 1, name: 'Dimma', src: 'data:image/jpeg;base64,'}, 
    {id: 2, name: 'Irina', src: "data:image/jpeg;base64,"}, 
    {id: 3, name: 'Vasial', src: "data:image/jpeg;base64,"}
  ],
}

const dialogsReducer = (state = initialState, action) => {

  switch (action.type) {
    
    case ADD_MESEGE: 
      return {...state,
        mesegeDate : [...state.mesegeDate, {id: 4, name: action.newMessegeBoddy}],
      };

    default:
      return state;
  }
}

export const adMessegeActionCreater = (newMessegeBoddy) => ({type: ADD_MESEGE, newMessegeBoddy});


export default dialogsReducer;